.main-content {
  position: relative;
  top: 280px;
}

.main-content__titles {
  margin: 0 auto;
  padding: 41px 20px 0;
  max-width: 940px;
}

.main-content__title {
  margin: 0 auto;
  margin-bottom: 31px;
  font-size: 24px;
  font-weight: 300;
  line-height: 28px;
  text-align: center;
}

@media screen and (min-width: 660px) {
  .main-content {
    top: 0;
  }
  .main-content__titles {
    padding: 49px 20px 48px;
  }
  .main-content__title {
    margin-bottom: 0;
    font-size: 40px;
    line-height: 48px;
  }
}
@media screen and (min-width: 1200px) {
  .main-content__titles {
    padding: 67px 10px 76px;
  }
  .main-content__title {
    font-size: 50px;
    line-height: 60px;
  }
}
.main-content__title:last-of-type {
  margin-bottom: 0;
}